import { motion } from 'framer-motion';
import WebApp from '@twa-dev/sdk';
import './App.css';

function App() {
  const handleShowAlert = () => {
    try {
      WebApp.showAlert('Это работает! 🎉');
    } catch (e) {
      alert('Это работает! 🎉 (Но мы не в Telegram)');
    }
  };

  const handleShowPopup = () => {
    try {
      WebApp.showPopup({
        title: 'Привет!',
        message: 'Это всплывающее окно Telegram',
        buttons: [
          {text: 'Супер!', type: 'ok'},
          {text: 'Закрыть', type: 'close'}
        ]
      });
    } catch (e) {
      alert('В браузере это было бы красивое окно Telegram 😉');
    }
  };

  const handleHapticFeedback = () => {
    try {
      WebApp.HapticFeedback.impactOccurred('heavy');
    } catch (e) {
      console.log('Вибрация работает только в Telegram');
    }
  };

  const buttonStyle = {
    padding: '20px 40px',
    fontSize: '18px',
    borderRadius: '15px',
    backgroundColor: 'rgba(255, 255, 255, 0.1)',
    color: 'white',
    cursor: 'pointer',
    backdropFilter: 'blur(10px)',
    textShadow: '1px 1px 2px rgba(0,0,0,0.1)',
    position: 'relative',
    overflow: 'hidden'
  };

  return (
    <div className="App">
      <motion.div
        initial={{ y: -100, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ duration: 0.8, type: "spring" }}
      >
        <h1>Привет, Telegram!</h1>
      </motion.div>

      <motion.div
        style={{ display: 'flex', gap: '20px', flexDirection: 'column' }}
      >
        <motion.button
          className="glowing-button"
          whileHover={{ 
            scale: 1.05,
            boxShadow: '0 0 25px rgba(255,255,255,0.4)'
          }}
          whileTap={{ scale: 0.95 }}
          onClick={handleShowAlert}
          style={buttonStyle}
          initial={{ opacity: 0, x: -100 }}
          animate={{ opacity: 1, x: 0 }}
          transition={{ delay: 0.2 }}
        >
          ✨ Нажми меня
        </motion.button>

        <motion.button
          whileHover={{ scale: 1.1, rotate: 5 }}
          whileTap={{ scale: 0.9 }}
          onClick={handleShowPopup}
          style={{
            padding: '20px 40px',
            fontSize: '18px',
            border: 'none',
            borderRadius: '10px',
            backgroundColor: '#179cde',
            color: 'white',
            cursor: 'pointer'
          }}
        >
          Открыть Popup
        </motion.button>

        <motion.button
          whileHover={{ 
            scale: 1.1,
            boxShadow: '0px 0px 8px rgb(255,255,255)' 
          }}
          whileTap={{ scale: 0.9 }}
          onClick={handleHapticFeedback}
          style={{
            padding: '20px 40px',
            fontSize: '18px',
            border: 'none',
            borderRadius: '10px',
            backgroundColor: '#27a7f7',
            color: 'white',
            cursor: 'pointer'
          }}
        >
          Вибрация
        </motion.button>
      </motion.div>

      <motion.div
        style={{ 
          position: 'fixed', 
          bottom: '20px',
          fontSize: '12px',
          opacity: 0.7 
        }}
        initial={{ opacity: 0 }}
        animate={{ opacity: 0.7 }}
        transition={{ delay: 1 }}
      >
        Тестовое приложение
      </motion.div>
    </div>
  );
}

export default App;
